<template>
  <div id="app">
    <el-container>
        <el-header>TestDapp</el-header>
        <el-main>

          <!--  -->
           <el-row>
            <el-col :span="6">
              <div class="connect-btn-bg">
                Wallet Address:
              </div>
            </el-col>
            <div class="connect-btn-bg">
              {{ getWalletAddress }}
            </div>
           </el-row>


          <el-row>
            <el-col :span="24">
              <div class="connect-btn-bg">
                Universal Link: {{ updateUniLink }}
              </div>
            </el-col>
          </el-row>

          <!--  -->
          <el-row :gutter="12">
            <el-col :span="12">
              <div class="connect-btn-bg">
                <el-input v-model="testProofPayload" placeholder="proofPayload"></el-input>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="connect-container">
                <el-button type="primary" v-on:click="connectBtnClicked" icon="el-icon-share" :disabled="false">{{ connectStatusStr }}</el-button>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="connect-container">
                <el-button type="primary" v-on:click="resotreConnectAction" icon="el-icon-share" :disabled="false">{{ "" }}</el-button>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="connect-container">
                <el-button type="primary" v-on:click="disconnectAction" icon="el-icon-share" :disabled="this.walletAddress.length == 0">{{ disconnectStatusStr }}</el-button>
              </div>
            </el-col>

          </el-row>

          <!-- KeyPair -->
          <el-row :gutter='12'>
            <el-col :span="12">
              <div class="connect-btn-bg">
                PubKey: <el-input v-model="localPubKey" placeholder="PublicKey" disabled></el-input>
              </div>
            </el-col>
            <el-col :span="12">
              <div class="connect-btn-bg">
                SecKey: <el-input v-model="localSecKey" placeholder="SecretKey" disabled></el-input>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="18">
              <div class="connect-btn-bg">
                <el-input v-model="testTransaction" placeholder="sendTransactions"></el-input>
              </div>
            </el-col>
            <el-col :span="6">
              <div class="connect-container">
                <el-button type="primary" v-on:click="sendTransaction()">{{ testTransactionStr }}</el-button>
              </div>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="24">
              <div class="connect-container">
                <el-button type="primary" v-on:click="clearCacheKeyPair()" icon="el-icon-delete"></el-button>
              </div>
            </el-col>
          </el-row>
        </el-main>
    </el-container>

  </div>
