<script>
// import * as sdk from "../../src/index"
import {OKXTonConnect, ConnectionManager, utils} from "../../src/index"
var TON = new OKXTonConnect({metaData: {
            name:"dapp",
            icon:"",
            url:""
        }})
var testCM = new ConnectionManager()
async function testconnect() {
  await testCM.connect()
}


async function testSendTransaction(){
  // await TON.connect()
  let transactionRequest = {"validUntil":1722481054071,"network":-239,"from":"12345","messages":[{"address":"EQARULUYsmJq1RiZ-YiH-IJLcAZUVkVff-KBPwEmmaQGH6aC","amount":"285000000","payload":"te6cckEBAgEAiAABbQ+KfqUAAACYOR4QzEBfXhAIAO87mQKicbKgHIk4pSPP4k5xhHqutqYgAB7USnesDnCcECwbgQMBAJclk4VhgBD3JEg1TUr75iTijBghOKm/sxNDXUBl7CD6WMut0Q85yAZ+V1MAKNBrbGziXTXC9CetaXjoHL28y7L1wbJwuhzumC3yzL7QVwlJkA=="}]}
  TON.sendTransaction(transactionRequest)
}

export default {
  name: 'App',
  data: ()=> {
    return {
      testTopic: '',
      testPublicKey: '',
      testProofPayload: '',
      pendingDeepLink: '-',
      logList: [],
      connectReady: false,
      connectStatusStr: '',
      disconnectStatusStr: '',
      localPubKey: '',
      localSecKey: '',
      testTransaction:'',
      testTransactionStr:'',
      walletAddress:'',
    }
  },
  computed: {
    updateUniLink:function(){
      console.log(`update uni link: ${this.testTopic} ${this.testPublicKey}`)
      let pendingDeepLink = utils.generateDeepLink(
        {
          protocolVer: "1",
          channelId: this.testTopic,
          clientId: this.testPublicKey,
          dAppInfo: {
            name: "test",
            url: "-",
            iconUrl: "-"
          },
          requestId:"0",
          testPayload: this.testProofPayload
        }
      )
      return pendingDeepLink
    },
    getWalletAddress:function (){
      return this.walletAddress;
    },


  },
  methods: {
    async connectBtnClicked() {
      console.log('get connect wallet btn clicked');
      this.testTonSdk();
    },
    async disconnectAction() {
      console.log('get disconnect wallet btn clicked');
      TON.disconnect();
    },
    async resotreConnectAction() {
      console.log('get restore wallet btn clicked');
      TON.restoreConnection();
    },
    clearCacheKeyPair: () => {
      console.log(`generate local key pair`)
      testCM.cleanCache()
      // testTonSdk()

    },
    sendTransaction:()=>{
      console.log('sendTransaction')
      testSendTransaction()
    },

    async testTonSdk(){
      console.log('---testProofPayload--->>>>----',this.testProofPayload);
      let proof = {
        tonProof:this.testProofPayload,
        redirect:"tg://resolve"
      };

      console.log('-------------testTonSdk------>>>>----',proof);

      await  TON.connect(proof);


    }

  },
  created() {
    // testEncrypt('test msg')
    console.log('test sdk here')
    console.log(TON)
  },
  mounted() {
    this.connectReady = true
    console.log('test connect called')
    testconnect()
    const keyPairInfo = testCM.getCryptoInfo()
    console.log(keyPairInfo)
    this.localPubKey = keyPairInfo.kp.publicKey
    this.localSecKey = keyPairInfo.kp.secretKey
    this.testPublicKey = this.localPubKey
    this.testTopic = keyPairInfo.topic?.content

    TON.onStatusChange(function (walletInfo) {
      console.log('--dapp ---->>>>----',walletInfo);
      if (walletInfo){
        this.walletAddress = walletInfo.account.address;
      }else {
        this.walletAddress = "";
      }

    }.bind(this));

  }
}
</script>

